.companyDetailsBody {
    background-color: #f0eeee;
    margin: 0;
    padding: 0; 
}

.mainContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.companyInfoContainer {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    width: 1000px;
    height: 300px;
    border-top: 2px solid;
    margin-top: 100px;
    background-color: #fff;
}

.companyInfo {

    display: flex;
    flex-direction: row;

}

.titleText2 {
    flex: 1;
    margin-left: 20px;
    
}

.titleText3 {
    flex: 1;
    
}

.titleText1 {
    padding-left: 20px;
    border-bottom: 1px solid rgba(0.1, 0.1, 0.1, 0.1);
    height: 40px;
    
}




.sendButton {
    margin-top: 50px;
    display: inline-block;
    padding: 10px 20px;
    font-size: 16px;
    font-weight: bold;
    text-align: center;
    /* text-decoration: none; */
    cursor: pointer;
    border: 2px solid #3498db;
    background-color: #fff;
    color: #3498db;
    border-radius: 30px;
    transition: background-color 0.5s, color 0.5s;
}

.sendButton:hover{

    background-color: #3498db;
    color: #fff;

}

.details{
    width: 1000px;
    height: auto;
    background-color: #fff;
    min-height: 500px;
    margin-top: 50px;
    
}
.details>p {
    padding: 20px;
}

.companyProfileContainer {

    width: 1000px;
    background-color: #fff;
    margin-top: 30px;
    height: auto;
    min-height: 300px;
}


.navBar {
    position: fixed;
    left: 0;
    top: 30%;
    transform: translateY(-50%);
    background-color: #333; 
    padding: 17px;
    display: flex;
    flex-direction: column;
    align-items: center;
    z-index: 1000;
    border-radius: 0 10px 10px 0;
    cursor: pointer;
}

.navItem {
    color: white; 
    text-decoration: none;
    font-size: 20px;
    margin-bottom: 10px; 
  }
  
  .navItem:hover {
    text-decoration: underline;
  }

.companyInfomation>p {
    
    padding-left: 20px;
    padding-top: 8px;
}

.companyProfileContainer {
    margin-bottom: 100px;
}




